import React, { useState } from 'react';
import { Card, CardImg, CardText, CardBody,
    CardTitle, BreadcrumbItem, Breadcrumb, Button } from 'reactstrap';
import { Link } from 'react-router-dom';
import SelectedDishCommentForm from '../../Forms/SelectedDishCommentForm';
import {Loading} from '../../Loading';
import { baseUrl } from '../../../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const DishDetail = ({selectedDish, comments, postComment, isLoading, ErrorMessage}) => {

    const [isCommentModalOpen, setIsCommentModalOpen] = useState(false);


    const renderDishImage = (dish) => {
        if (dish != null)
            return(
                <FadeTransform in
                    transformProps={{
                        exitTransform: 'scale(0.5) translateY(-50%)'
                    }}>
                    <Card>
                        <CardImg top src={ baseUrl + dish.image} alt={dish.name} />
                        <CardBody>
                        <CardTitle>{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                        </CardBody>
                    </Card>
                </FadeTransform>
            );
        else
            return(
                <div></div>
            );
    };

    const renderDishComments = (comments) => {

        if (comments != null)
        {
            let renderComments = comments.map((comment, idx) => {
                if (comment != null)
                {
                    const date = new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)));
                    return (
                        <Fade in>
                            <li key={idx}>
                                <p>{comment.comment}</p>
                                <p>-- {comment.author}, {date}</p>
                            </li>
                        </Fade>
                    );
                } else {
                    return (
                        <></>
                    );
                }
            });

            return (
                <>
                    <h4>Comments</h4>
                    <ul className="list-unstyled" >
                        <Stagger in>
                        {renderComments}
                        </Stagger>
                    </ul>
                    {renderSubmitCommentBtn()}
                    <SelectedDishCommentForm isModalOpen={isCommentModalOpen} toggleModal={toggleSubmitCommentModal} submitComment={handleSubmit}
                    />
                </>
            );
        } else {
            return (
                <></>
            );
        }
    };

    const renderDish = (dish, comments) => {
        if (isLoading) {
            return (
                <div className="row">
                    <div className="container">
                        <Loading />
                    </div>
                </div>
            );
        }
        else if (ErrorMessage) {
            return (
                <div className="row">
                    <div className="container">
                        <h4>{ErrorMessage}</h4>
                    </div>
                </div>
            );
        } else {
            return (
                <>
                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                            <BreadcrumbItem active>{selectedDish.name}</BreadcrumbItem>
                        </Breadcrumb>
                    </div>
                    <div className="row">
                        <div className="col-12">
                            <h4>{dish.name}</h4>
                        </div>
                        <div  className="col-12 col-md-5 m-1">
                            {renderDishImage(dish)}
                        </div>
                        <div  className="col-12 col-md-5 m-1">
                            {renderDishComments(comments)}
                        </div>
                    </div>
                </>
            );
        }
    };

    const toggleSubmitCommentModal = () => setIsCommentModalOpen(!isCommentModalOpen);

    const renderSubmitCommentBtn = () => {
        return (
            <Button outline onClick={toggleSubmitCommentModal}>
                <span className="fa fa-pencil fa-lg"></span> Submit Comments
            </Button>
        );
    };

    const handleSubmit = values => {
        // alert(JSON.stringify(value));
        postComment(selectedDish.id, values.rating, values.author, values.comment);
    };

    return (
        <div className="container">
            {renderDish(selectedDish, comments)}
        </div>
    );

};

export default DishDetail;