import React, { useState } from 'react';
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem, Jumbotron, Button, ModalBody, ModalHeader, Modal, Label, Input , Form, FormGroup} from 'reactstrap';
import {useForm} from 'react-hook-form';
import { NavLink } from 'react-router-dom';
import { NAVITEMS } from '../../shared/NavigationItems';


const Header = () => {

    const [isOpen, setIsOpen] =useState(false);
    const [isModalOpen, setIsOpenModal] = useState(false);
    const {register, handleSubmit} = useForm();

    const toggleModal = () => setIsOpenModal(!isModalOpen);

    const toggleNav = () => setIsOpen(!isOpen);

    const handleLogin = data => {
        toggleModal();
        alert(JSON.stringify(data));
    };

    const LoginForm = () => {
        return (
            <div>
                <Modal isOpen={isModalOpen} toggle={toggleModal}>
                    <ModalHeader toggle={toggleModal}>Login</ModalHeader>
                    <ModalBody>
                        <Form onSubmit={handleSubmit(handleLogin)}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username" innerRef={register}/>
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password" innerRef={register}/>
                            </FormGroup>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" id="remember" name="remember"innerRef={register}/>Remember me?
                                </Label>
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Login</Button>
                        </Form>
                    </ModalBody>
                </Modal>
            </div>
        );
    };

    const renderNavItems = NAVITEMS.map((item, idx) => {
        return (
            <NavItem key={idx}>
                <NavLink className="nav-link" to={item.pathTo}>
                    <span className={item.iconClass}></span>{item.text}
                </NavLink>
            </NavItem>
        );
    });

    return (
        <div>
            <Navbar dark expand="md">
                <div className="container">
                    <NavbarToggler onClick={toggleNav} />
                    <NavbarBrand className="mr-auto" href="/">
                        <img src="../../assets/images/logo.png" alt="logo" height="30" width="41" />
                    </NavbarBrand>
                    <Collapse isOpen={isOpen} navbar>
                        <Nav navbar>
                            {renderNavItems}
                        </Nav>
                        <Nav className="ml-auto" navbar>
                            <NavItem>
                                <Button outline onClick={toggleModal}>
                                    <span className="fa fa-sign-in fa-lg"></span> Login
                                </Button>
                            </NavItem>
                        </Nav>
                    </Collapse>
                </div>
            </Navbar>
            <Jumbotron>
                <div className="container">
                    <div className="row row-header">
                        <div className="col12 col-sm-6">
                            <h1>Ristorante Con Fusion</h1>
                            <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                        </div>
                    </div>
                </div>
            </Jumbotron>
            <LoginForm />
        </div>
    );
}

export default Header;
