import React from 'react'
import {ICONS} from '../../shared/footerIcon';
import {Link} from 'react-router-dom';
import { NAVITEMS } from '../../shared/NavigationItems';


const Footer = () => {

    const renderIcons = ICONS.map((icon, idx) => {

        return (
            <a className={icon.btnClasName} href={icon.href} key={idx}>
                <i className={icon.IconClassName}></i>
            </a>
        );
    });

    const renderNavItems = NAVITEMS.map((item, idx) => {
        return (
            <li key={idx}><Link to={item.pathTo}>{item.text}</Link></li>
        );
    });

    return (
        <div className="footer">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-4 offset-1 col-sm-2">
                        <h5>Links</h5>
                        <ul className="list-unstyled">
                            {renderNavItems}
                        </ul>
                    </div>
                    <div className="col-7 col-sm-5">
                        <h5>Our Address</h5>
                        <address>
                        121, Clear Water Bay Road<br />
                        Clear Water Bay, Kowloon<br />
                        HONG KONG<br />
                        <i className="fa fa-phone fa-lg"></i>: +852 1234 5678<br />
                        <i className="fa fa-fax fa-lg"></i>: +852 8765 4321<br />
                        <i className="fa fa-envelope fa-lg"></i>: <a href="mailto:confusion@food.net">
                            confusion@food.net</a>
                        </address>
                    </div>
                    <div className="col-12 col-sm-4 align-self-center">
                        <div className="text-center">
                            {renderIcons}
                        </div>
                    </div>
                </div>
                <div className="row justify-content-center">
                    <div className="col-auto">
                        <p>© Copyright 2018 Ristorante Con Fusion</p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Footer;
