import React from 'react';
import { Col, Row, Label, Button, Modal, ModalHeader, ModalBody } from 'reactstrap';
import {Control, LocalForm, Errors} from 'react-redux-form';

const SelectedDishCommentForm = ({isModalOpen, toggleModal, submitComment}) => {

    const required  = (val) => val && val.length;
    const maxLength = (len) => (val) => !(val) || (val.length <= len);
    const minLength = (len) => (val) => val && (val.length >= len);

    return (
        <Modal isOpen={isModalOpen} toggle={toggleModal}>
            <ModalHeader toggle={toggleModal}>Submit Comments</ModalHeader>
            <ModalBody>
                <LocalForm onSubmit={(values) => submitComment(values)}>
                    <Row className="form-group">
                        <Col>
                            <Label htmlFor="rating">Rating</Label>
                            {/* eslint-disable-next-line */}
                            <Control.select className="form-control" model=".rating" type="text" id="rating" name="rating">
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                                <option value="5">5</option>
                            </Control.select>
                        </Col>
                    </Row>
                    <Row className="form-group">
                        <Col>
                            <Label htmlFor="author">Your Name</Label>
                            {/* eslint-disable-next-line */}
                            <Control.text model=".author" className="form-control" type="text" id="author" name="author" placeholder="Your Name"
                            validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}/>
                            <Errors className="text-danger" model=".author" show="touched"
                            messages={{ required: 'Required ', minLength: 'Must be >= 3 chars ', maxLength: 'Must be <=15 chars'}} />
                        </Col>
                    </Row>
                    <Row check>
                        <Col>
                            {/* eslint-disable-next-line */}
                            <Control.textarea model=".comment" className="form-control" rows="6" type="textarea" id="comment" name="comment"/>
                        </Col>
                    </Row>
                    <Row>
                        <Col className="mt-2">
                            <Button type="submit" name="buttonSubmitComment" color="primary">Submit</Button>
                        </Col>
                    </Row>
                </LocalForm>
            </ModalBody>
        </Modal>
    );
};

export default SelectedDishCommentForm;