import React from 'react';
import { Col, Row, Label, Button } from 'reactstrap';
import {Control, Errors, Form} from 'react-redux-form';

const ContactUsFormRedux = ({resetFeedbackForm, postFeedbacks}) => {

    const handleSubmit = value => {
        alert(JSON.stringify(value));
        alert(typeof postFeedbacks);
        postFeedbacks(value.firstName, value.lastName, value.telnum, value.email, value.agree, value.contactType, value.message);
        // postFeedback(value.)
        resetFeedbackForm();
    };

    const required  = (val) => val && val.length;
    const maxLength = (len) => (val) => !(val) || (val.length <= len);
    const minLength = (len) => (val) => val && (val.length >= len);
    const isNumber  = (val) => !isNaN(Number(val));
    const validEmail = val => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

    return (
        <Form model="feedback" onSubmit={(values) => handleSubmit(values)}>
        <Row className="form-group">
            <Label md={2} htmlFor="firstName">First Name</Label>
            <Col md={10}>
                {/* eslint-disable-next-line */}
                <Control.text type="text" model=".firstName" className="form-control"
                name="firstName" placeholder="First Name"
                validators={{
                    required, minLength: minLength(3), maxLength: maxLength(15)
                }}/>
                <Errors className="text-danger" model=".firstName" show="touched"
                messages={{ required: 'Required', minLength: 'Must be >= 3 chars', maxLength: 'Must be <=15 chars'}} />
            </Col>
        </Row>
        <Row className="form-group">
            <Label md={2} htmlFor="lastName">Last Name</Label>
            <Col md={10}>
                {/* eslint-disable-next-line */}
            <Control.text type="text" model=".lastName" className="form-control"
                name="lastName" placeholder="last Name"
                validators={{
                    required, minLength: minLength(3), maxLength: maxLength(15)
                }}/>
            <Errors className="text-danger" model=".firstName" show="touched"
                messages={{ required: 'Required', minLength: 'Must be >= 3 chars', maxLength: 'Must be <=15 chars'}} />
            </Col>
        </Row>
        <Row className="form-group">
            <Label md={2} htmlFor="telnum">Tel No.</Label>
            <Col md={10}>
                {/* eslint-disable-next-line */}
            <Control.text type="tel" model=".telnum" className="form-control"
                name="telnum" placeholder="Tel.No"
                validators={{
                    required, minLength: minLength(3), maxLength: maxLength(15), isNumber
                }}/>
            <Errors className="text-danger" model=".firstName" show="touched"
                messages={{ required: 'Required', minLength: 'Must be >= 3 chars', maxLength: 'Must be <=15 chars', isNumber: 'Must be all numbers'}} />
            </Col>
        </Row>
        <Row className="form-group">
            <Label md={2} htmlFor="email">Email</Label>
            <Col md={10}>
                {/* eslint-disable-next-line */}
            <Control.text type="email" model=".email" className="form-control"
                name="email" placeholder="Email"
                validators={{
                    required, validEmail
                }}/>
                <Errors className="text-danger" model=".firstName" show="touched"
                messages={{ required: 'Required', validEmail: 'Must be valid email format'}} />
            </Col>
        </Row>
        <Row className="form-group">
            <Col md={{size:6, offset:2}}>
                <div className="form-check">
                    <Label check>
                        {/* eslint-disable-next-line */}
                        <Control.checkbox model=".agree"
                        name="agree" className="form-check-input"/>{' '}
                        <b>May we Contact you?</b>
                    </Label>
                </div>
            </Col>
            <Col md={{size:3, offset:1}}>
                {/* eslint-disable-next-line */}
                <Control.select model=".contactType" name="contactType">
                    <option value="Email">Email</option>
                    <option value="Tel.No">Tel.No</option>
                </Control.select>
            </Col>
        </Row>
        <Row className="form-group">
            <Label md={2} htmlFor="message">Your Feedback</Label>
            {/* eslint-disable-next-line */}
            <Col md={10}><Control.textarea model=".message" className="form-control" type="textarea" name="message" rows="12" /></Col>
        </Row>
        <Row className="form-group">
            <Col md={{size:10, offset:2}}>
                <Button type="submit" name="buttonSubmit" color="primary" >Send Feedback</Button>
            </Col>
        </Row>
    </Form>
    );
};

export default ContactUsFormRedux;