import React, { useEffect } from 'react';
import Menu from '../Menu';
import Header from '../Header';
import Footer from '../Footer';
import Home from '../Home';
import DishDetail from '../Menu/DishDetail';
import Contact from '../Contact';
import About from '../AboutUs';
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { postComment, fetchDishes, fetchComments, fetchPromos, fetchLeaders, postFeedback } from '../../redux/ActionCreators';
import { actions } from 'react-redux-form';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

const mapStateToProps = state => {

    return {
        dishes: state.dishes,
        promotions: state.promotions,
        leaders: state.leaders,
        comments: state.comments,
    }
}

const mapDispatchToProps = (dispatch) => ({
    postComment: (dishId, rating, author, comment) => dispatch(postComment(dishId, rating, author, comment)),
    // fetchDishes: () => dispatch(fetchDishes())
    fetchDishes() {dispatch(fetchDishes())},
    fetchComments() {dispatch(fetchComments())},
    fetchPromos() {dispatch(fetchPromos())},
    fetchLeaders() {dispatch(fetchLeaders())},
    resetFeedbackForm() { dispatch(actions.reset('feedback'))},
    postFeedback(firstname, lastname, telnum, email, agree, contactType, message) {
        dispatch(postFeedback(firstname, lastname, telnum, email, agree, contactType, message))
    }
});

const Main = (props) => {

    useEffect(() => {
        props.fetchDishes();
        props.fetchComments();
        props.fetchPromos();
        props.fetchLeaders();
        //  eslint-disable-next-line
    }, [])

    // console.log(typeof props.resetFeedbackForm);
    const DishWithId = ({match}) => {

        return (
            <DishDetail
                selectedDish={props.dishes.dishes.filter(dish => dish.id === parseInt(match.params.dishId, 10))[0]}
                isLoading={props.dishes.isLoading}
                ErrorMessage={props.dishes.errorMessage}
                comments={props.comments.comments.filter(comm => comm.dishId === parseInt(match.params.dishId, 10))}
                commentsErrorMessage={props.comments.errorMessage}
                postComment={props.postComment}
            />
        );
    };

    const HomePage = () => {
        return (
            <Home
            dish={props.dishes.dishes.filter(dish => dish.featured)[0]}
            dishesLoading={props.dishes.isLoading}
            dishesErrorMessage={props.dishes.errorMessage}
            promotion={props.promotions.promotions.filter(promo => promo.featured)[0]}
            promosLoading={props.promotions.isLoading}
            promosErrorMessage={props.promotions.errorMessage}
            leader={props.leaders.leaders.filter(lead => lead.featured)[0]}
            leadersLoading={props.leaders.isLoading}
            leadersErrorMessage={props.leaders.errorMessage}/>
        );
    }
    return (
        <div>
            <Header />
            <TransitionGroup>
                <CSSTransition key={props.location.key} classNames="page" timeout={300}>
                    <Switch>
                        <Route path="/home" component={HomePage} />
                        <Route exact path="/menu" component={() => <Menu dishes={props.dishes.dishes} isLoading={props.dishes.isLoading} errorMessage={props.dishes.errorMessage}/>} />
                        <Route path="/menu/:dishId" component={DishWithId} />
                        <Route exact path="/contactus" component={() => <Contact resetFeedbackForm={props.resetFeedbackForm} postFeedback={props.postFeedback}/>} />
                        <Route exact path="/aboutus" component={() =>
                        <About
                        leaders={props.leaders.leaders}
                        leadersLoading={props.leaders.isLoading}
                        leadersErrorMessage={props.leaders.errorMessage} />} />
                        <Redirect to="/home"/>
                    </Switch>
                </CSSTransition>
            </TransitionGroup>

            <Footer />
        </div>
    );
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));